body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  display: none;
}

/*--- Navigation --*/

.link {
  border: 0;
  color: white;
  -webkit-text-decoration: "none";
          text-decoration: "none";
}

.link:hover {
  color: #ffa000;
}

@font-face {
  font-family: "Nexa Bold";
  src: url(/static/media/NexaDemo-Bold.6aecd77f.otf);
}
@font-face {
  font-family: "Nexa Light";
  src: url(/static/media/NexaDemo-Light.8e16c92f.otf);
}
@font-face {
  font-family: "Montserrat";
  src: url(/static/media/Montserrat-Regular.9c460951.ttf);
}

.nexa-bold {
  font-family: "Nexa Bold";
}

.nexa-light {
  font-family: "Nexa Light";
}

.montserrat {
  font-family: "Montserrat";
}

h2 {
  text-align: center;
  font-size: 4rem !important;
  font-family: "Nexa Bold", Helvetica, sans-serif;
  letter-spacing: 0.1rem;
}

h3 {
  text-align: center;
  font-size: 2.6rem !important;
  font-family: "Montserrat", Helvetica, sans-serif;
  font-weight: bold !important;
  color: #59260b !important;
}

h4 {
  font-size: 2rem !important;
  font-family: "Montserrat", Helvetica, sans-serif;
}

p {
  font-family: "Montserrat", Helvetica, sans-serif;
  padding: 1rem;
  margin: auto;
}

.em1 {
  color: #ffa000;
}

hr {
  border-color: rgba(0, 0, 0, 0.2) !important;
}

.w-80 {
  width: 80%;
  margin: auto;
}

.page {
  position: absolute;
  padding-top: 4.6rem;
  left: 0;
  right: 0;
}

.section-gray {
  padding-top: 3rem;
  padding-bottom: 3rem;
  background-color: rgba(0, 0, 0, 0.1);
}

.section-white {
  padding-top: 3em;
  padding-bottom: 3rem;
  background-color: white;
}

@media (max-width: 991px) {
  .w-80 {
    width: 100%;
    margin: auto;
  }
}

.home-inner {
  -webkit-flex: 1 1;
          flex: 1 1;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: "black";
  background-image: url(/static/media/initiation-f2018.b9ffacc0.jpg);
  background-position: center;
  top: 0;
  z-index: -1;
}

.overlay {
  background-color: rgba(0, 0, 0, 0.75);
  width: 100%;
  height: 100%;
}

.caption {
  width: 100%;
  max-width: 100%;
  position: absolute;
  top: 38%;
  z-index: 1;
  color: white;
  text-transform: uppercase;
}

.caption h1 {
  font-size: 5rem;
  letter-spacing: 0.3rem;
  text-shadow: 0.1rem 0.1rem 0.8rem black;
  padding-bottom: 1rem;
}

.caption h4 {
  font-size: 2rem;
  letter-spacing: 0.2rem;
  text-shadow: 0.1rem 0.1rem 0.5rem black;
  padding-bottom: 1.6rem;
}

.btn {
  border-width: medium !important;
  padding: 0.6rem 1.4rem !important;
  font-size: 1.1rem !important;
}

@media (max-width: 767px) {
  .caption h1 {
    font-size: 3rem;
    letter-spacing: 0.1rem;
    padding-bottom: 0.5rem;
  }

  .caption h4 {
    font-size: 1.4rem !important;
    padding-bottom: 1.6rem;
  }

  .btn-lg {
    padding: 0.5rem 1rem;
    font-size: 1rem;
  }
}

.fade-appeart,
.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-appear-active,
.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0;
  -webkit-transition: opacity 150ms linear;
  transition: opacity 150ms linear;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

